html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  border: 0;
}

body {
  font-family: var(--font-inter);
  color: var(--dark-text);
  background-color: var(--dark-bg);
  margin: 0;
  padding: 0;
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(90deg,hsl(329, 70%, 58%) 0%, hsl(5, 77%, 71%) 60%, hsl(37, 97%, 70%) 100%);
  --youtube: hsl(348, 97%, 39%);
  --dark-toggle:hsl(210, 78%, 56%) to hsl(146, 68%, 55%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-subtext: hsl(228, 34%, 66%);
  --dark-text: hsl(0, 0%, 100%);
  --light-toggle: hsl(230, 22%, 74%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-subtext: hsl(228, 12%, 44%);
  --light-text: hsl(230, 17%, 14%);
}

:root {
  --font-inter:"Inter", sans-serif;
}

.container {
  display: block;
  max-width: 69.375rem;
  margin: 2.25rem auto 2.875rem;
  padding: 0 1.5625rem;
}

.container h2 {
  margin-bottom: 1.5rem;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 64em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: var(--dark-card-bg);
  width: 100%;
  border-radius: 5px;
  text-align: center;
  margin: 0 auto;
  padding: 1.5625rem;
  cursor: pointer;
}
.card__platform {
  margin: 0.3125rem auto 1.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__followers {
  margin: 0 auto 1.5625rem auto;
}
.card__count {
  font-size: 56px;
  font-weight: 700;
}
.card__lable, .card__username {
  color: var(--dark-subtext);
  margin-bottom: 0.25rem;
  font-size: 12px;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__number {
  font-size: 0.75rem;
}
.card__number--up {
  color: var(--limegreen);
}
.card__number--down {
  color: var(--brightred);
}
.card--facebook {
  border-top: 0.2rem solid var(--facebook);
}
.card--youtube {
  border-top: 0.2rem solid var(--youtube);
}
.card--twitter {
  border-top: 0.2rem solid var(--twitter);
}
.card--instagram {
  border-image-source: var(--instagram);
  border-top: 0.2rem solid;
  border-image-slice: 40;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__subtitle {
  color: var(--dark-subtext);
  font-size: 0.875rem;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}
.card-grid .card__change, .card-grid .card__count {
  align-self: end;
}
.card-grid .card__count {
  font-size: 2rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.header__title h1 {
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
}
.header__title span {
  color: var(--dark-subtext);
}